// lib/gemini.ts
import { GoogleGenerativeAI } from "@google/generative-ai"

// A chave API não é usada no lado do cliente para esta função, mas mantemos o objeto
// genAI para consistência, caso outras funções precisem dele.
// Usamos a variável NEXT_PUBLIC_GEMINI_API_KEY do .env
const genAI = new GoogleGenerativeAI(process.env.NEXT_PUBLIC_GEMINI_API_KEY || "")

// Usamos a variável NEXT_PUBLIC_URL_API do .env para montar o URL do backend
const API_URL = process.env.NEXT_PUBLIC_URL_API || "http://localhost:3001"

export async function chatWithGemini(userMessage: string, selectedItems: any[]) {
  try {
   const response = await fetch(`${API_URL}/gemini`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        userMessage: userMessage, 
      }),
    })

    if (!response.ok) {
        // log de erro caso de b.0
        if (response.status === 400) {
            const errorData = await response.json();
            console.error("Erro 400 no backend:", errorData.erro);
        }
      throw new Error("Erro ao chamar backend Gemini")
    }

    const data = await response.json()
    
    // 3. Retornamos a chave 'resposta' (conforme o seu backend Express)
    return data.resposta 
  } catch (error) {
    console.error("Erro no chatWithGemini:", error)
    throw new Error("Erro ao gerar resposta do chatbot")
  }
}
